

.projet-contener {
    font-family: Arial, Helvetica, sans-serif;
    line-height:2;
    background-color: white;
    margin-top: 0px;
}

.projet-arrow-and-title {
    
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin: 0;
    
}

.titre-and-barreH {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

}

.arrow-car {
    width: 45px;
    
}

.arrow-car.R {
    padding-right: 20px;
}

.arrow-car.L {
  padding-left: 20px;  
}

.projet-contener h1 {
    text-align: center;
    margin: 0;
    font-size: 42px;

}

.barre-h {
    width: 250px;
    height: 2px;
    background-color: #2081d8;
}

.projet-contener h2 {
    font-size: 34px;  
}

.projet-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.content-para-barre {
    padding: 0 20px;
}

.projet-content .p-desc {
    font-size: 18px;
    margin-top: -5px;
}

.projet-content img {
    width: 80%;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
}

.contener-competence {
    display: flex;
    justify-content: flex-start;
    width: 100%;

}

.projet-competence-content {
    margin: 50px 0 50px 80px;

}

#tech-utilise {
    padding: 10px 20px;
    background-color: #5c2e37;
    color: white;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
}

#competence-utilise {
    padding: 10px 20px;
    background-color: #2081d8;
    color: white;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
}

@media (max-width: 768px) {

    * {
        box-sizing: border-box; 
        
    }

    .projet-contener {
        margin-top: -1px;
    }


    .projet-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
    }
    
    .projet-contener h1 {
        text-align: center;
        margin: 0;
        font-size: 34px;
    
    }
    
    .projet-contener h2 {
        font-size: 28px;  
    }

    .content-para-barre {
        padding: 0;
    }
    
    .projet-content .p-desc {
        padding: 0 20px;
        text-align: justify;
    }
    
    .projet-content img {
        width: 300px;
    }
    
    .contener-competence {
        flex-direction: column;
        align-items: center;
        
    }
    
    .projet-competence-content {
        margin: 20px 0 50px 0px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 8px;
    }
    

}